body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 20px;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

head {
  display: none;
}
* {
  box-sizing: border-box;
}

/* Clearfix */
header::before,
header::after {
  display: table;
  content: '';
}

header::after {
  clear: both;
}

.col-3, .col-4, .col-6, .col-12 {
  float: left;
  padding-left: 1.04166666%;
  padding-right: 1.04166666%;
}

/*Mobile*/
.col-3, .col-4, .col-6, .col-12 {
  width: 100%;
}

nav {
  
}

@media only screen and (min-width: 640px) {
  .col-3 {
    width: 25%;
  }
  
  .col-4 {
    width: 33.3333333%;
  }

  .col-6 {
    width: 50%;
  }

  .col-12 {
    width: 100%;
  }
}
* {
  font-family: 'Montserrat', sans-serif;
  text-decoration: none;
}

nav {
  background-color: #49274A;
  margin: 0;
  overflow: hidden;
}

nav div {
  -webkit-align-items: center;
          align-items: center;
}

nav ul {
  padding: 0;
  float: left;
}

nav li {
  margin: 0;
  text-align: center;
  font-size: 17px;
  display: inline;
}

nav li a {
  color: #F8EEE7;  
  font-weight: bold;
  padding-left: 20px;
  padding-right: 20px;
}

nav li a:hover {
  color: #94618E;
}

nav li a:active {
  color: #94618E;
}

.authLink a {
  color: #F8EEE7;
}

@media (max-width: 500px) {
  .small-menu-className {
    display: inline-block;
    text-align: center;
  }

  .small-menu-className i {
    color: white;
    padding: 5px;
  }

  .small-menu-className a {
    padding: 4px;
    font-size: 14px;
  }

  .small-menu-className li {
    display: block;
    margin-left: 0;
    padding: 5px 0;
  }
}


* {
  text-align: center;
}

.landingContainer {
  padding: 20px;
}

header {
  padding: 20px;
  color: #49274A;
}

h2 {
  display: block;
  width: 100%;
  color: #49274A;
}

h3 {
  display: block;
  width: 100%;
  color: #49274A;
}

p {
  color: #94618E;
}

h4 {
  color: #94618E;
}
label {
  color: #49274A;
  font-size: 20px;
  font-weight: bolder;
}

input {
  margin-left: 8px;
  margin-right: 8px;
  width: 100%;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 2px solid #49274A;
  border-radius: 4px;
  background-color: #F8EEE7;
  color: #94618E;
  font-size: 20px;
  font-weight: bolder;
}

input:focus {
  border: 3px solid #F8EEE7;
}

button {
  background-color: #49274A;
  border: 2px solid #94618e;
  border-radius: 4px;
  color: #F4DECB;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-weight: bolder;
  font-size: 18px;
  margin-top: 20px;
}

button:hover {
  background-color: #F8EEE7;
  color: #49274A;
}
footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #49274A;
  color: #F4DECB;
  text-align: center;
}

footer a {
  color: #F4DECB;
}

footer a:hover {
  color: #94618E;
}

